@import '../../styles/utils/mixins';
@import '../../styles/utils/vars';

.header {
  position: sticky;
  top: 0;
  background-color: $white;
  display: flex;
  flex-direction: row;
  align-items: center;
  box-sizing: border-box;
  box-shadow: 0px 1px 1px $elements;
  margin-bottom: 1px;
  height: 64px;
  z-index: 2;

  &__main-link {
    flex: none;
    order: 0;
    flex-grow: 0;
    margin-left: 24px;
    margin-top: 18px;
    margin-bottom: 18px;
    margin-right: 48px;
  }

  &__main-icon {
    width: 80px;
    height: 28px;
  }

  &__container-left-side {
    display: flex;
    margin-left: auto;
  }

  &__left-side-icons {
    padding: 0 24px;
    justify-content: center;
    box-shadow: -1px 0px 0px $elements;
    background-color: #fff;
    border: 0;
    cursor: pointer;
  }

  &__icon {
    position: relative;
    width: 16px;
    height: 16px;
    transition: transform 0.3s;

    &:hover {
      transform: scale(1.3);
    }
  }

  &__button-counter {
    position: absolute;
    left: 50%;
    right: 20%;
    bottom: 45%;
    width: 14px;
    height: 14px;
    font-weight: 600;
    font-size: 9px;
    line-height: 12px;
    color: $white;
    background-color: $accent;
    border: 2px solid $white;
    border-radius: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
}

.nav {
  &__list {
    display: flex;
    padding: 0;
    margin: 0;
  }

  &__item {
    margin-right: 64px;
    position: relative;
    color: #060b35;
    list-style: none;
    height: 100%;
  }

  &__link {
    font-family: 'Mont';
    font-style: normal;
    font-weight: 800;
    font-size: 12px;
    line-height: 46px;
    text-decoration: none;
    text-transform: uppercase;

    color: $secondary;
  }
}

.is-active {
  color: $primary;
}

.is-active::after {
  content: '';
  display: flex;
  justify-content: flex-end;
  width: 100%;
  height: 2px;
  background-color: $primary;
}

@include onTablet() {
  .header {
    height: 48px;

    &__left-side-icons {
      padding: 0 16px;
    }

    &__main-link {
      margin-left: 16px;
      margin-top: 13px;
      margin-bottom: 13px;
      margin-right: 32px;
    }

    &__main-icon {
      width: 64px;
      height: 22px;
    }
  }

  .nav {
    &__item {
      margin-right: 32px;
      position: relative;
      color: #060b35;
      list-style: none;
      height: 100%;
    }
  }

  .is-active::after {
    top: 26px;
  }
}

@include onPhone() {
  .header {
    height: 48px;

    &__left-side-icons {
      padding: 0 16px;
    }

    &__main-link {
      margin-left: 16px;
      margin-top: 13px;
      margin-bottom: 13px;
      margin-right: 32px;
    }

    &__main-icon {
      width: 64px;
      height: 22px;
    }
  }

  .nav {
    display: none;
  }
}
